.crsr {
  position: fixed;
  top: -3.125rem;
  left: -3.125rem;
  transform: translate(-49.55%, -50.25%);
  mix-blend-mode: difference;
  pointer-events: none;
  z-index: 9999;
}
.crsr::before {
  content: "";
  width: 5.625rem;
  height: 5.625rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transform: scale(0.15);
  background: var(--white);
  box-shadow: 0 0 1.25rem cyan, 0 0 3.75rem cyan, 0 0 6.25rem cyan;
  border: 0.0625rem solid cyan;
  animation: colors 7s infinite;
  transition: all 200ms;
  transition-property: content transform opacity border background;
  transition-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
}

.crsr.brnd {
  mix-blend-mode: unset;
}
.crsr.brnd::before {
  content: "";
  transform: scale(0.85);
  opacity: 0.25;
  border: none;
  background: linear-gradient(45deg, black, blue);
}
@media screen and (max-width: 39.9375em) {
  .crsr.brnd::before {
    transform: scale(0.75);
  }
}

.crsr.hmbrgr {
  mix-blend-mode: unset;
}
.crsr.hmbrgr::before {
  content: "";
  transform: scale(0.85);
  opacity: 0.15;
  border: none;
  background: linear-gradient(45deg, black, blue);
}
@media screen and (max-width: 39.9375em) {
  .crsr.hmbrgr::before {
    transform: scale(0.75);
  }
}

.crsr.nvlnks {
  mix-blend-mode: unset;
}
.crsr.nvlnks::before {
  content: "";
  transform: scale(0.85);
  opacity: 0.15;
  border: none;
  background: linear-gradient(45deg, black, blue);
}

.crsr.bnnrdstntnlnks {
  mix-blend-mode: unset;
}
.crsr.bnnrdstntnlnks::before {
  content: "";
  transform: scale(0.85);
  opacity: 0.25;
  border: none;
  background: linear-gradient(45deg, black, blue);
}
@media screen and (max-width: 39.9375em) {
