.prldr {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background: #252755;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 9;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
  transition-delay: 1s;
  transition-delay: 1s;
}
.prldr .ldbr {
  width: 0%;
  height: 100%;
  max-height: 0.375rem;
  background: linear-gradient(45deg, #09e7e7, #252755);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  overflow: hidden;
}
.prldr figure {
  width: 37.5rem;
  height: 37.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fade 300ms;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
  position: relative;
}
.prldr figure span {
  width: 100%;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  color: lightcyan;
  text-align: center;
  letter-spacing: 0.1875rem;
  font-family: fantasy;
  font-size: 1rem;
}
.prldr figure img {
  height: 6.25rem;
  display: inline-block;
  opacity: 0.05;
}

@keyframes fade {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.pgldd .prldr {
  height: 0;
}
.pgldd .prldr .ldbr {
  max-height: 100%;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
}
.pgldd .prldr figure {
  transform: translateY(50px);
  opacity: 0;
}

